<style scoped>
    .hero{
        min-height: 100vh;
        display: flex;
        align-items: center;
    }
</style>
<script>
// @ is an alias to /src
import axios from "axios"
export default {
  name: 'Country',
  data(){
    return{
       country: '',
    }
  },
  mounted(){
      var url  = "https://corona.lmao.ninja/countries/"
      var id = this.$route.params.id
      axios
      .get(url+id)
      .then(response=>(
          this.country = response['data'],
          console.log(response['data'])
      ))
  },
  components: {
    
  }
}
</script>